$backgroundPrimary: #2f005b;
$accent: #5600a8;
$font-primary: 'Avenir';
$accentNeutral: #e1d7ed;



.header {
	display: block;
	position: absolute;
	top: 0;
	left: 0;
	box-sizing: border-box;
	width: 100%;
	background-color: $backgroundPrimary;
	transition: background .4s ease-in;
}

@keyframes slide1{
	0% {
		transform: translateY(-200px);
		opacity: 0;
		display: none;

	}
	100% {
		transform: translate(0);
		opacity: 1;
	}
}



.header-inner {
	display: flex;
	flex-direction: row;
	box-sizing: border-box;
	align-items: center;
	text-align: center;
	justify-content: center;
	padding-top: 20px;
	z-index: -1;
}

.header-inner a:nth-child(1) {
	animation: 1.8s ease-out 0s 1 slide1;
}

.header-inner a:nth-child(2) {
	animation: 1.8s ease-out 0s 1 slide1;
}

.header-inner a:nth-child(3) {
	animation: 1.8s ease-out 0s 1 slide1;
}

.header-inner a:nth-child(4) {
	animation: 1.8s ease-out 0s 1 slide1;
}

.header-inner a:nth-child(5) {
	animation: 1.8s ease-out 0s 1 slide1;
}

.header-inner a {
	text-transform: uppercase;
	font-size: 12px;
	font-weight: 700;
	text-decoration: none;
	color: $accentNeutral;
	padding-top: 15px;
	padding-bottom: 15px;
	padding: 10px;
	font-family: $font-primary;
}

.header-inner a:hover, {
	color: $accentNeutral;
	letter-spacing: 5px;
}

.header-inner a {
	transition: all 0.6s;
}


* { 
	box-sizing: border-box;
}

body {
	font-family: $font-primary;
	background-color: $backgroundPrimary;
	line-height: 1.8;
	padding: 1em;
	font-weight: 500;

}

h2 p {
	text-align: left;
	font-size: 14px;
	font-weight: 600;
	margin-left: 20px;
}

.container {
	max-width: 1170px;
	margin-left: auto;
	margin-right: auto;
	padding: 1em;
}

.site {
	animation: 1.8s ease-out 0s 1 title-text;
	text-align: center;
	font-weight: 300;
	margin-top: 60px;
	color: $accentNeutral;
}

.site span {
	color: $accent;
	font-weight: 700;
	animation: 1.8s ease-out 0s 1 title-span;
}

.wrapper {
	box-shadow: 0 0 20px 0 rgba(82, 95, 110, 0.8);
	padding: 1em;
	border-radius: 12px;
	background-color: $accentNeutral;
	animation: 1.8s ease-out 0s 1 form-animate;
}


.contact form {
	display: grid;
	grid-template-columns: repeat(auto-fill, 50%);
	text-align: center;
	color: $accent;
}

.contact form label {
	display: block;
	text-align: left;
	margin-left: 11%;
}

.contact form p {
	margin-top: 20px;
}

.contact form .full {
	grid-column: 1 / 3;
	margin-top: 20px;
}

.contact form .message {
	grid-column: 1 / 3;
}

#message {
	height: 120px;
}

.confirmation {
	padding: 20px;
	margin: 0 auto;
	display: none;
	width: 60%;
	color: $accent;
	box-shadow: 0 0 20px 0 rgba(82, 95, 110, 0.8);
	background-color: $accentNeutral;
	border: 4px solid $accentNeutral;
	border-radius: 6px;
	text-align: center;
	font-size: 30px;
}

.contact form input {
	width: 80%;
	background-color: $accent;
	border-radius: 6px;
	border: 0;
	padding: 1.5em;
	color: $accentNeutral;
}

::placeholder {
	color: $accentNeutral;
}

button {
	cursor: pointer;
	width: 120px;
	padding: 10px;
	border-radius: 5px;
	border: 1px solid $accentNeutral;
	text-transform: uppercase;
}

button:hover, button:focus {
	background-color: $accent;
	color: white;
	box-shadow: 0 0 20px 0 rgba(82, 95, 110, 0.3);
	transition: background-color 0.6s ease-out;
}

.contact form input:hover, .contact form input:focus {
	border: 1px solid $backgroundPrimary;
}

small {
	margin-left: 11%;
	text-align: left;
	color: red;
	display: block;
	font-size: 9px;
}

@keyframes title-text {
	0% {
		transform: translateX(300px);
	}

	100% {
		transform: translateX(0);
	}
}

@keyframes title-span {
	0% {
		opacity: 0;
	}

	100% {
		opacity: 1;
	}
}

@keyframes form-animate {
	0% {
		opacity: 0;
		transform: translateY(500px);
	}

	100% {
		opacity: 1;
		transform: translateY(0);
	}
}

@media screen and (max-width: 767px) {
	.confirmation {
		width: 90%;
		text-size: 25px;
	}

	.contact form p {
		margin-top: 6px;
	}

	.contact form .full {
		margin-top: 6px;
	}

	#message {
		height: 100px;
	}

}

@media screen and (min-width: 768px) {
	.confirmation {
		width: 80%;
		text-size: 30px;
	}

	.contact form p {
		margin-top: 12px;
	}

	.contact form .full {
		margin-top: 12px;
	}

	#message {
		height: 100px;
	}

	max-width: 992px;
}